.page {
  color: #060b35;
  font-family: Roboto, sans-serif;
}

.page__body {
  margin: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo-link {
  display: flex;
}

.header__logo-img {
  width: 40px;
  height: 40px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:last-child a {
  margin: 0;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  margin-right: 20px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s;
  display: block;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.catalog {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .catalog {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .catalog {
    --column-count: 4;
  }
}

.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
}

.card__product-image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__product-title {
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s;
}

.card__product-code {
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__product-rating {
  justify-content: space-between;
  align-items: end;
  margin: 0 0 24px;
  display: flex;
}

.card__product-rating .reviews-count {
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__product-price {
  justify-content: space-between;
  align-items: center;
  margin: 0 0 16px;
  display: flex;
}

.card__product-price .price-title {
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__product-price .price-numbers {
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button-buy {
  box-sizing: border-box;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.card__button-buy:hover {
  color: #00acdc;
  background-color: #fff;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__product-title {
  color: #34568b;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.0aa15587.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin: 0;
}

.stars--0 .stars__star:nth-child(-n), .stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.1a24ce14.svg");
}
